.slide{
  background-color: rgb(150, 196, 50);
  width: 800px;
  height: 200px;
  overflow: auto;
  border-radius:5px;
}
button{
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color:rgb(7, 15, 246)!important;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px whitesmoke, 0 6px 20px 0 whitesmoke;
  transition-duration:4ms;
}
button:hover {background-color:rgb(236, 9, 244)!important;
}
.button:active {
  background-color: red;
  box-shadow: 0 5px rgb(102, 102, 102);
  transform: translateY(4px);
}